import { Paper, TextField, Typography } from '@mui/material';
import React, { ChangeEvent, FunctionComponent } from 'react';
import BasicButton from '../../button/BasicButton';

import './index.scss';

interface Props {
  name: string;
  onChangeName: (input: string) => void;
  password: string;
  onChangePassword: (input: string) => void;
  onClickSignIn: () => void;
  isSigningIn: boolean;
}

const SignInPage: FunctionComponent<Props> = ({
  name, password, onClickSignIn,
  onChangeName, onChangePassword, isSigningIn,
}) => {
  return (
    <div className='sign-in-page-comp'>
      <div className="centered">
        <Paper
          className="container"
          elevation={5}
        >
          <Typography
            variant="h3"
            align="center"
            style={{
              marginBlock: '20px',
            }}
            fontSize="36px"
          >
            CallabT
          </Typography>
          <TextField
            label="이름"
            variant="outlined"
            fullWidth
            margin="normal"
            size="small"
            type="text"
            value={name}
            onChange={(event: ChangeEvent<HTMLInputElement>) => onChangeName(event.target.value)}
          />
          <TextField
            label="비밀번호"
            variant="outlined"
            fullWidth
            margin="normal"
            size="small"
            type="password"
            style={{ marginBottom: '20px' }}
            value={password}
            onChange={(event: ChangeEvent<HTMLInputElement>) => onChangePassword(event.target.value)}
          />
          <BasicButton
            isLoading={isSigningIn}
            onClick={onClickSignIn}
          >
            로그인
          </BasicButton>
        </Paper>
      </div>
    </div>
  );
};

export default SignInPage;
