import React, { FunctionComponent } from 'react';

import MaterialDrawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import AssignmentIcon from '@mui/icons-material/Assignment';
import FaceIcon from '@mui/icons-material/Face';

interface Props {
  isOpened: boolean;
  onClose: () => void;
  listItems: ListItem[];
  onClickListItem: (index: number) => void;
}

export interface ListItem {
  text: string;
  icon: 'mail' | 'notice' | 'face';
  linkUrl: string;
}

const Drawer: FunctionComponent<Props> = ({
  isOpened, onClose, listItems, onClickListItem,
}) => {
  return (
    <MaterialDrawer
      anchor="left"
      open={isOpened}
      onClose={onClose}
    >
      <List>
        {listItems.map(({ text, icon }, index) => (
          <ListItem key={text} onClick={() => onClickListItem(index)} >
            <ListItemButton>
              <ListItemIcon>
                {icon === 'mail' && <ContactMailIcon />}
                {icon === 'notice' && <AssignmentIcon />}
                {icon === 'face' && <FaceIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Divider />
    </MaterialDrawer>
  );
};

export default Drawer;
