import { CallabtAPI } from '@callabt/core';
import React, { FunctionComponent, useState } from 'react';

import SignInPage from '../../view/component/page/SignInPage';

interface Props {
  onSignedIn: (props: { accessToken: string }) => void;
}

const SignInPageContainer: FunctionComponent<Props> = ({
  onSignedIn,
}) => {
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [isSigningIn, setIsSigningIn] = useState(false);

  const onClickSignIn = async () => {
    if (!isSigningIn) {
      if (name.length === 0) {
        alert('이름을 입력해 주세요');
        return;
      }
  
      if (password.length < 8) {
        alert('비밀번호를 8자리 이상 입력해주세요.');
      }
  
      setIsSigningIn(true);
      try {
        const { data: { accessToken } } = await CallabtAPI.IssueManagerToken.issueManagerTokenV1({ name, password });

        onSignedIn({ accessToken });
      } catch(error) {
        console.error(error);
        if (error instanceof CallabtAPI.CallabtApiError) {
          if (error.errorCodes.includes(CallabtAPI.IssueManagerToken.ErrorCode.NO_NAME)) {
            alert('이름을 입력해 주세요.');
          } else if (error.errorCodes.includes(CallabtAPI.IssueManagerToken.ErrorCode.NO_PASSWORD)) {
            alert('비밀번호를 입력해 주세요.');
          } else if (error.errorCodes.includes(CallabtAPI.IssueManagerToken.ErrorCode.WRONG_PASSWORD)) {
            alert('비밀번호가 틀렸습니다.');
          } else {
            alert('알 수 없는 오류가 발생했습니다. 다시 시도해 주세요.');
          }
        } else {
          alert('로그인 중 오류가 발생했습니다. 다시 시도해 주세요.');
        }
      }
      setIsSigningIn(false);
    }
  };

  return (
    <SignInPage
      name={name}
      onChangeName={setName}
      password={password}
      onChangePassword={setPassword}
      onClickSignIn={onClickSignIn}
      isSigningIn={isSigningIn}
    />
  );
};

export default SignInPageContainer;
