import React, { PropsWithChildren, FunctionComponent } from 'react';
import BasicAppBar from '../../app-bar/BasicAppBar';
import Drawer, { ListItem } from '../../drawer/Drawer';

interface Props {
  isDrawerOpened: boolean;
  onCloseDrawer: () => void;
  onClickMenuButton: () => void;
  drawerListItems: ListItem[];
  managerName: string;
  onClickSignOut: () => void;
  onClickDrawerListItem: (index: number) => void;
  onClickHome: () => void;
}

const PageLayout: FunctionComponent<PropsWithChildren<Props>> = ({
  isDrawerOpened, onCloseDrawer, onClickMenuButton, children, drawerListItems,
  managerName, onClickSignOut, onClickDrawerListItem, onClickHome,
}) => {
  return (
    <div style={{ paddingTop: '64px' }}>
      <BasicAppBar
        onClickMenuButton={onClickMenuButton}
        name={managerName}
        onClickSignOut={onClickSignOut}
        onClickHome={onClickHome}
      />
      <Drawer
        isOpened={isDrawerOpened}
        onClose={onCloseDrawer}
        listItems={drawerListItems}
        onClickListItem={onClickDrawerListItem}
      />
      {children}
    </div>
  );
};

export default PageLayout;
